	module.exports = {"data":{"challengeNode":{"title":"Use Media Queries to Create Responsive Layouts","description":["通过使用媒体查询重新排列网格区域，更改网格尺寸以及重新排列网格项位置，CSS 网格能轻松地使网站更具响应性。","在最右侧的预览区中，当网页可视区域的宽不小于 300px 时，列数从 1 变为 2。并且，广告（advertisement）区域完全占据左列。","<hr>","当网页可视区域的宽不小于<code>400px</code>时，使 header 区域完全占据最顶行，footer 区域完全占据最底行。"],"challengeType":0,"fields":{"slug":"/responsive-web-design/css-grid/use-media-queries-to-create-responsive-layouts","blockName":"CSS Grid","tests":[{"text":"当网页可视区域的宽在<code>400px</code>及以上时，<code>container</code>类应该有<code>grid-template-areas</code>属性且使得 footer 和 header 区域分别占据顶行和底行，advert 和 content 区域分别占据中间行的左和右列。","testString":"assert(code.match(/@media\\s*?\\(\\s*?min-width\\s*?:\\s*?400px\\s*?\\)[\\s\\S]*.container\\s*?{[\\s\\S]*grid-template-areas\\s*?:\\s*?\"\\s*?header\\s*?header\\s*?\"\\s*?\"\\s*?advert\\s*?content\\s*?\"\\s*?\"\\s*?footer\\s*?footer\\s*?\"\\s*?;[\\s\\S]*}/gi), '当网页可视区域的宽在<code>400px</code>及以上时，<code>container</code>类应该有<code>grid-template-areas</code>属性且使得 footer 和 header 区域分别占据顶行和底行，advert 和 content 区域分别占据中间行的左和右列。');"}]},"required":[],"files":{"indexhtml":{"key":"indexhtml","ext":"html","name":"index","contents":"<style>\n  .item1 {\n    background: LightSkyBlue;\n    grid-area: header;\n  }\n  \n  .item2 {\n    background: LightSalmon;\n    grid-area: advert;\n  }\n  \n  .item3 {\n    background: PaleTurquoise;\n    grid-area: content;\n  }\n  \n  .item4 {\n    background: lightpink;\n    grid-area: footer;\n  }\n  \n  .container {\n    font-size: 1.5em;\n    min-height: 300px;\n    width: 100%;\n    background: LightGray;\n    display: grid;\n    grid-template-columns: 1fr;\n    grid-template-rows: 50px auto 1fr auto;\n    grid-gap: 10px;\n    grid-template-areas:\n      \"header\"\n      \"advert\"\n      \"content\"\n      \"footer\";\n  }\n  \n  @media (min-width: 300px){\n    .container{\n      grid-template-columns: auto 1fr;\n      grid-template-rows: auto 1fr auto;\n      grid-template-areas:\n        \"advert header\"\n        \"advert content\"\n        \"advert footer\";\n    }\n  }\n  \n  @media (min-width: 400px){\n    .container{\n      /* 请修改本行以下的代码 */\n    \n      grid-template-areas:\n        \"advert header\"\n        \"advert content\"\n        \"advert footer\";\n    \n    /* 请修改本行以上的代码*/\n    }\n  }\n</style>\n  \n<div class=\"container\">\n  <div class=\"item1\">header</div>\n  <div class=\"item2\">advert</div>\n  <div class=\"item3\">content</div>\n  <div class=\"item4\">footer</div>\n</div>","head":"","tail":""},"indexjs":null,"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/responsive-web-design/css-grid/create-grids-within-grids","id":"5a94fe7769fb03452672e463"},"slug":"/responsive-web-design/css-grid/use-media-queries-to-create-responsive-layouts"}}

